@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);
* {
  box-sizing: border-box;
}

body {
  background-color: #fff;
  margin: 0;
  padding: 0;
  font-family: 'Inter', sans-serif;
}

button, input {
  outline: none;
}


.result {
    max-width: 1440px;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #F5F5F7;
}

.result__img {
    width: 50%;
    height: auto;
}

.main__result {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.main__result_title {
    margin: 0;
    margin-bottom: 50px;
    font-style: normal;
    font-weight: 600;
    font-size: 3rem;
    line-height: 116%;
}

.main__result_subtitle {
    opacity: 0.5;
    margin-bottom: 10px;
    font-style: normal;
    font-weight: 600;
    font-size: 2rem;
    line-height: 116%;
}

.main__result_btn {
    width: 296px;
    height: 64px;
    background: #FF8B37;
    border-radius: 12px;
    color: #FFF;
    border: none;
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 116%;
}

.main__result_btn:hover {
    background: #FFAC70;
}

.main__result_btn:active {
    background: #E87928;
}

@media screen and (max-width: 820px) {
    .result {
        flex-direction: column;
    }
    .main__result {
        align-items: center;
        justify-content: center;
    }
    .main__result_title.main__result_subtitle {
        text-align: center;
    }
}

@media screen and (max-width: 620px) {
    .result__img {
        width: 100%;
    }
}

@media screen and (max-width: 280px) {
    .main__result_btn {
        width: 80%;
    }
}
.main {
    max-width: 1440px;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(to right bottom, #FFF 50%, #FFF3EB 50%);    
}

.main__img {
    width: 60%;
    height: auto;
}

.main__start {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 1.5rem;
}

.main__start_title {
    font-style: normal;
    font-weight: 600;
    font-size: 3rem;
    line-height: 116%;
}

.main__start_btn {
    width: 296px;
    height: 64px;
    background: #FF8B37;
    border-radius: 12px;
    color: #FFF;
    border: none;
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 116%;
}

.main__start_btn:hover {
    background: #FFAC70;
}

.main__start_btn:active {
    background: #E87928;
}

@media screen and (max-width: 820px) {
    .main {
        flex-direction: column;
    }
    .main__start {
        align-items: center;
        justify-content: center;
    }
    .main__start_title {
        text-align: center;
    }
}

@media screen and (max-width: 620px) {
    .main__img {
        width: 100%;
    }
}

@media screen and (max-width: 280px) {
    .main__start_btn {
        width: 80%;
    }
}
.board {
    max-width: 1440px;
    min-height: 100vh;
    display: flex;
}

.board__game {
    width: 1100px;
    height: 100vh;
    display: flex;
    position: relative;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #E5E5E5;
}

.board__icon {
    display: none;
}

.board__icon_close {
    display: none;
}

.board__question {
    font-style: normal;
    font-weight: 600;
    font-size: 1.2rem;
    margin: 0 2rem;
    text-align: center;
}

.board__answers {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    margin-top: 200px;
}

.board__answer_btn {
    display: flex;
    align-self: start;
    border: 1px solid #D0D0D8;
    border-radius: 20px;
    background-color: #fff;
    width: 400px;
    padding: 10px;
    margin: 10px 5px;
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 100%;
    cursor: pointer;
}

.board__answer_btn:hover {
    border: 1px solid #FF8B37;
}

.board__answer_btn:active {
    background-color: #FFF3EB;
    border: 1px solid #FF8B37;
}

.board__answer_btn.correct_answer {
    background-color: #E6FAEA;
    border: 1px solid #47D867;
}

.board__answer_btn.wrong_answer {
    background-color: #FDEEED;
    border: 1px solid #EC6259;
}

.board__answer_letter {
    color: orange;
    font-weight: 600;
    font-size: 15px;
    margin-right: 10px;
}

.board__answer {
    margin: 0;    
    padding: 0;
    font-weight: normal;
    font-size: 15px;
    margin-right: 10px;
}

.board__steps {
    width: 376px;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.board__steps_list {
    display: flex;
    flex-direction: column-reverse;
    list-style-type: none;
    padding: 0;
}

.board__steps_item  {
    margin-top: 8px;
    padding: 5px 50px;
    border: 1px solid #D0D0D8;
    border-radius: 20px;
    background-color: #fff;
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 130%;
    text-align: center;
}

.finished_step {
    color: #D0D0D8;
}

.board__steps_item.current_step {
    color: #FF8B37;
    border: 1px solid #FF8B37;
}



@media screen and (max-width: 860px) {
    .board__steps {
        display: none;
    }
    .board__icon {
        width: 30px;
        height: 35px;
        border: none;
        background-color: #e5e5e5;
        display: inline-block;
        top: 15px;
        right: 15px;
        position: absolute;
        margin: 0;
        padding: 0;
        font-size: 1.5rem;
        cursor: pointer;
    }
    .board__icon_close {
        width: 30px;
        height: 35px;
        border: none;
        background-color: #e5e5e5;
        margin: 0;
        padding: 0;
        font-size: 1.5rem;
        cursor: pointer;
        display: inline-block;
        position: fixed;
        top: 15px;
        right: 15px;
    }
    .board__steps_active {
        position: fixed;
        top: 0;
        right: 0;
        width: 100%;
        height: 100vh;
        background-color:#e5e5e5;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}

@media screen and (max-width: 420px) {
    .board__game {
        width: 100%;
    }
    .board__answer_btn {
        width: 100%;
        margin: 10px 20px;
    }
}
